nav.main ul ul {
  display: none;
  position: absolute;
  top: 100%;
  left: -0.5rem;
  width: 10rem; }

nav.main ul ul ul {
  top: -0.5rem;
  right: 100%;
  left: inherit; }

nav.main ul li {
  display: block;
  list-style: none outside;
  position: relative; }

nav.main > ul > li > ul li:not(:last-child) {
  padding-bottom: 0.5rem; }

nav.main > ul > li {
  padding: 0 0.5rem 0 0; }

nav.main ul li a {
  display: block;
  padding: 0.5rem 1rem;
  border-radius: 0.4rem;
  text-decoration: none;
  color: #333; }

nav.main ul li a:hover {
  color: #EB1D45;
  -webkit-transition: color 0.6 ease-out 1s;
  transition: color 0.6 ease-out 1s; }

nav.main ul li a:before {
  content: "";
  position: absolute;
  width: 100%;
  height: 2px;
  bottom: 0;
  left: 0;
  background-color: #EB1D45;
  visibility: hidden;
  -webkit-transform: scaleX(0);
  transform: scaleX(0);
  -webkit-transition: all 0.3s ease-in-out 0s;
  transition: all 0.3s ease-in-out 0s; }

nav.main ul li a:hover:before {
  visibility: visible;
  -webkit-transform: scaleX(1);
  transform: scaleX(1); }

#main-nav-toggle,
#toggle {
  display: none; }

#toggle {
  position: relative;
  -webkit-box-flex: 1;
  -ms-flex: 1 0 100%;
  flex: 1 0 100%;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  -ms-flex-line-pack: center;
  align-content: center;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  padding: 1rem 0;
  -webkit-animation: navtogglein 0.5s 1 ease-in-out forwards;
  animation: navtogglein 0.5s 1 ease-in-out forwards; }

#toggle i.fas {
  display: inline-block;
  margin-right: 0.4rem; }

#toggle i.fa-times {
  display: none; }

@media screen and (max-width: 800px) {
  #toggle {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-flex: 0;
    -ms-flex: 0 0 auto;
    flex: 0 0 auto; }
  nav.main {
    display: none;
    border-top: 2px solid #000; }
  nav.main ul li:not(:first-child) {
    padding-top: 0.5rem; }
  #main-nav-toggle:checked + header nav.main {
    display: block;
    -webkit-box-flex: 1;
    -ms-flex: 1 0 100%;
    flex: 1 0 100%;
    margin-top: 1rem;
    text-align: center;
    -webkit-animation: navtogglein 0.5s 1 ease-in-out forwards;
    animation: navtogglein 0.5s 1 ease-in-out forwards; }
  #main-nav-toggle:checked + header nav.main ul li a {
    -webkit-animation: slideIn 1s ease 1 both;
    animation: slideIn 1s ease 1 both; }
  #main-nav-toggle:checked + header #toggle i.fa-bars {
    display: none;
    -webkit-animation: navtogglein 0.5s 1 ease-in-out forwards;
    animation: navtogglein 0.5s 1 ease-in-out forwards; }
  #main-nav-toggle:checked + header #toggle i.fa-times {
    display: block;
    -webkit-animation: navtogglein 0.5s 1 ease-in-out forwards;
    animation: navtogglein 0.5s 1 ease-in-out forwards; } }

@media screen and (min-width: 800px) {
  nav.main ul {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex; }
  nav.main ul li:hover > ul {
    display: block; } }

